          <p>
            <b>Administratorem danych osobowych jest "W & W " WOJCIECH WICZKOWSKI</b>
            <b>Artylerzystów 6, 84-300 Lębork</b>
            <b> posługująca się numerem NIP 8411010984.</b>
          </p>
            <p>
              <b>Użytkownikiem jest każdy podmiot korzystający ze strony internetowej i/lub przesyłający dane za
                pomocą
                formularzy
                na
                stronie </b>
            </p>
            <p>
              <b>Jakie informacje o użytkownikach gromadzi nasza strona internetowa i w jaki sposób może je
