            <p> Dane osobowe Użytkowników są przetwarzane przez Administratora danych w celu prawidłowej obsługi
              zapytania i
              realizacji usług.
              Podanie danych osobowych jest każdorazowo dobrowolne. Do umów powierzenia danych zawartych w ramach
              działalności
              Administratora
              zaliczamy:</p>
            <ol>
              <li>
                <p> Korespondencja będąca odpowiedzią na przesłanie zapytań przez formularze kontaktowe,</p>
              </li>
              <li>
                <p> Zawarcie i realizacja umowy sprzedaży,</p>
              </li>
              <li>
                <p> Przesyłanie wiadomości zawierającej informacje handlowe (za uprzednią zgodą wyrażoną w formie
                  zapisu na
                  Newsletter),</p>
              </li>
            </ol>
            <p> Żadne z&nbsp;powyższych informacji nie będą przekazywane osobom trzecim w celach innych niż
              niezbędne do
              wykonania
              zamówienia/usługi
              etc.
            </p>
            <p> Adresy IP naszych użytkowników są wykorzystywane w takich przypadkach jak: diagnoza problemów
