              pracą
              naszego
              serwera/analizy naruszeń bezpieczeństwa czy też w związku z&nbsp;zarządzaniem naszą stroną
              internetową. Adres
              IP
              może
              być również wykorzystywany do gromadzenia czy też identyfikacji danych demograficznych osób, które
              odwiedzają
              naszą
              stronę
              internetową.</p>
            <p> W&nbsp;szczególnych przypadkach możemy dopuścić sporządzenia zbiorczych raportów/ ogólnych zestawień
