              które
              zostaną ujawnione osobom trzecim (podane zestawienia nie zawierają danych pozwalających na
              identyfikację
              użytkowników
              witryny).
            </p>
            <p> Uzyskiwane dane możemy przekazywać podmiotom trzecim w przypadku, gdy te podmioty świadczą na naszą
              rzecz:
              usługi
              związane
              z obsługą naszej strony internetowej, usługi związane z realizacją umów zawartych poprzez naszą stronę
              internetową,
              usługi
              związane ze zbieraniem danych statystycznych, czy też usługi marketingowe (w postaci remarketingu). W
              każdym
              takim
              przypadku
              podstawą przekazania danych jest zagwarantowanie przez podmiot zewnętrzny gwarancji poufności danych
