              jego
              bazach.</p>
            <p> Możemy przekazywać uzyskane dane do państw spoza Europejskiego Obszaru Gospodarczego tylko w
              przypadku, gdy
              łączy
              nas umowa
              z usługodawcą mającym swoją siedzibę poza Europejskim Obszarem Gospodarczym. W każdym takim przypadku
              sprawdzamy czy
              państwo trzecie zapewnia odpowiedni poziom ochrony danych osobowych.</p>
            <p>
              <b>Cookies</b>
            </p>
            <p> Nasza witryna internetowa wykorzystuje pliki cookies, które służą ułatwieniu korzystania z zasobów
              naszej
              strony.
              Cookies
              zawierają użyteczne informacje i są przechowywane na komputerze Użytkownika - nasz serwer może je
              odczytać
              przy
              ponownym
              połączeniu z tym komputerem. Nasza strona internetowa wykorzystuje cookies w celach statystycznych,
              marketingowych
              (w
              postaci remarktingu) oraz w celu zapewnienia prawidłowego funkcjonowania strony internetowej.</p>
            <p> Użytkownik ma możliwość skonfigurowania przeglądarki internetowej w taki sposób, aby uniemożliwiała
              ona
              przechowywanie plików
              cookies na urządzeniu Użytkownika. Informacje dotyczące ustawień przeglądarki są dostępne w jej menu
              lub na
              stronie
              producenta.
              Wyłączenie informacji cookies w przeglądarce nie pozbawia Użytkownika dostępu do zasobów naszej strony
              internetowej,
              jednak może powodować utrudnienia w korzystaniu z niej.</p>
            <p> Pliki cookies mogą być przez Użytkownika usunięte po ich zapisaniu, poprzez odpowiednie funkcje
              przeglądarki
              internetowej,
              programy służące w tym celu lub skorzystanie z odpowiednich narzędzi dostępnych w ramach systemu
              operacyjnego,
              z
              którego
              korzysta Użytkownik.</p>
            <p> Więcej o plikach cookies przeczytasz m.in. pod adresem:
              <a href="https://pl.wikipedia.org/wiki/HTTP_cookie"
                 data-order="30">https://pl.wikipedia.org/wiki/HTTP_cookie</a>.
            </p>
            <p>
              <b>Informacja o politykach prywatności dostawców zewnętrznych</b>
            </p>
            <ol>
              <li>
                <p> Google –
                  <a href="https://www.google.pl/intl/pl/policies">https://www.google.pl/intl/pl/policies</a>
                </p>
              </li>
            </ol>
            <p>
              <b>Ochrona prywatności osób niepełnoletnich.</b>
            </p>
            <p> Nasza strona internetowa nie monitoruje i nie weryfikuje informacji na temat wieku swojej
              społeczności.
              Informacje
              kontaktowe
              od osób odwiedzających naszą stronę internetową (takie jak adresy poczty elektronicznej użytkowników)
