              wykorzystywane
              zgodnie z celem w związku z którym zostały one pozyskane. Osoby niepełnoletnie nie powinny wysyłać
              żadnych
              informacji
              ani subskrybować usług świadczonych przez nasz serwis bez wiedzy swoich rodziców lub opiekunów.</p>
            <p>
              <b>Zabezpieczenia&nbsp;</b>
              <br> Nasza strona internetowa zaopatrzona jest w środki bezpieczeństwa, mające na celu ochronę danych
              pozostających
              pod naszą
              kontrolą przed utratą, niewłaściwym wykorzystaniem czy też modyfikacją. Zobowiązujemy się chronić
              wszelkie
              informacje
              ujawnione nam przez Użytkowników zgodnie z&nbsp;normami ochrony bezpieczeństwa i zachowania poufności.
              Prawa
              dostępu
              do danych osobowych użytkowników serwisu zostały w&nbsp;restrykcyjny sposób ograniczone tak, aby
              informacje te
              nie
              znalazły
              się w rękach osób niepowołanych. Dostęp do danych osobowo-adresowych ma tylko ograniczona liczba
              uprawnionych
              osób
              na
              zasadach zgodnych z przepisami dotyczącymi ochrony danych osobowych osób fizycznych.</p>
            <p>
              <b>Prawa osób fizycznych, których dane osobowe są przetwarzane</b>
            </p>
            <p> Informujemy, że w każdym przypadku przysługuje Państwu prawo do żądania: dostępu do swoich danych
              osobowych,
              ich
              sprostowania,
              usunięcia, ograniczenia przetwarzania lub wniesienia sprzeciwu wobec ich przetwarzania lub złożyć
              skargę do
              organu
              nadzorczego.
              Mogą również Państwo żądać przeniesienia swoich danych osobowych do innego podmiotu.</p>
            <p> Administrator danych ma prawo udostępnić dane osobowe Użytkownika podmiotom upoważnionym na
              podstawie
              określonych
              przepisów
              prawa (np. organom ścigania).</p>
            <p> O ile przepisy prawa nie stanowią inaczej, Administrator danych przechowuje dane osobowe Użytkownika
              tak
              długo jak
              jest to
